
#home{
	position: relative;
	width: 100%;
	height: 100%;
	color: #fff;
	/* box-sizing: border-box; */
	overflow-x: hidden;
	background: rgba(7,17,27,0.95);
}
.canvas {
    position: fixed;
    z-index: 1;
}
#home a{
	color: #fff;
	display: block;
}
.content{
	/* height: 100%; */
	/* margin-top: -50px; */
	display: flex;
}
.content section{
	flex: 1;
	z-index: 11;
	margin-left: 200px;
	/* box-sizing: border-box; */
	/* padding-right: 60px; */
}
table{
	position: absolute;
	top: 100px;
	padding: 0 20px;
	width: 100%;
}
table thead tr{
	border-bottom: 2px solid #fff;
}
table tbody tr{
	border-bottom: 1px solid #fff;
}
table tr{
	height: 50px;
}
table tr td{
	text-align: center;
	cursor: pointer;
}
table tr td:last-child{
	width: 10%;
}
table tr th{
	font-weight: normal;
	cursor: pointer;
}
.search-bar{
	position: absolute;
	top: 20px;
	left: 10%;
	width: 400px;
	height: 46px;
	line-height: 46px;
}
.search-bar .input-wrap{
	float: left;
	width: 270px;
	height: 40px;
	padding: 0 15px;
	line-height: 40px;
	border: none;
	outline: none;
	font-size: 20px;
	border-radius: 20px 0 0 20px;
	background: rgba(255, 255, 255, 0.2);
	transition: all .8s;
}
.search-bar .input-wrap:focus{
	background: rgba(255, 255, 255, .8);
}
.search-bar .input-button{
	float: left;
	width: 50px;
	height: 38px;
	line-height: 38px;
	text-align: center;
	font-size: 14px;
	border: 1px solid rgba(255, 255, 255, 0.2);
	transition: all .8s;
}
.search-bar .input-wrap:focus .input-botton{
	border-color: rgba(255, 255, 255, 0.8);
}
.fade-enter-active {
  	transition: all .5s
}
.fade-enter, .fade-leave-active {
  	opacity: 0;
  	transform: rotate( -70deg ) translateX( 300px );
}




