
#menu[data-v-deb2e7bc] {
	height: 100%;
	overflow: hidden;
	position: relative;
}
.canvas[data-v-deb2e7bc] {
    position: fixed;
    z-index: -1;
}
.logo-bg[data-v-deb2e7bc] {
	width: 800px;
    height: 200px;
	position: absolute;
	z-index: 10000;
	top: 80px;
	left: 50%;
	transform: translateX(-50%);
	background: url('../assets/images/top_logo.png') no-repeat;
}
.nav[data-v-deb2e7bc] {
	width: 200px;
    height: auto;
    position: absolute;
    z-index: 10000;
    top: 350px;
    left: 50%;
    margin-left: -100px;
}
.gv[data-v-deb2e7bc] {
	text-decoration: none;
    background: url('../assets/images/nav_gv.png') repeat 0px 0px;
    width: 130px;
    height: 43px;
    display: block;
    text-align: center;
    line-height: 43px;
    cursor: pointer;
    float: left;
    margin: 10px 2px 10px 2px;
    font: 18px/43px 'microsoft yahei';
    color: #066197;
}
.gvname[data-v-deb2e7bc] {
	text-decoration: none;
    width: 300px;
    height: 43px;
    display: block;
    text-align: center;
    line-height: 43px;
    cursor: pointer;
    float: right;
    margin: 20px -15px 10px 2px;
    font: 18px/43px 'microsoft yahei';
    color: #09c218;
}
a.gv[data-v-deb2e7bc]:hover { 
	background: url('../assets/images/nav_gv.png') repeat 0px -43px; 
	color:#1d7eb8;
