	transition-duration: 0.5s;
}
.city[data-v-deb2e7bc] {
	width: 100%;
	height: 170px;
    position: fixed;
    bottom: 0px;
    z-index: 100;
    background: url('../assets/images/city.png') no-repeat;
    background-size: cover;
}
.moon[data-v-deb2e7bc] {
	width: 100px;
	height: 100px;
	position: absolute;
	left: 100px;
	top: 100px;
	background: url('../assets/images/moon.png') no-repeat;
	background-size: cover;
}
.fade-out-enter-active[data-v-deb2e7bc], .fade-out-leave-active[data-v-deb2e7bc] {
  	transition: all .5s
}
.fade-out-enter[data-v-deb2e7bc], .fade-out-leave-active[data-v-deb2e7bc] {
  	opacity: 0;
  	transform: translateX(-400px);
}
.fade-in-enter-active[data-v-deb2e7bc], .fade-in-leave-active[data-v-deb2e7bc] {
  	transition: all .5s
}
.fade-in-enter[data-v-deb2e7bc], .fade-in-leave-active[data-v-deb2e7bc] {
  	opacity: 0;
  	transform: translateX(400px);
}



