
#header[data-v-60e458e0]{
	position: relative;
	height: 80px;
	line-height: 80px;
	display: flex;
	padding: 0 20px;
	justify-content: space-between;
	z-index: 12;
}
.logo[data-v-60e458e0]{
	font-size: 26px;
	font-weight: 600;
}
.logo span[data-v-60e458e0]{
	font-size: 16px;
	margin-left: 15px;
}
.user[data-v-60e458e0]{
	font-size: 16px;
}
.user .avatar[data-v-60e458e0]{
	width: 50px;
	height: 50px;
	border-radius: 50%;
	vertical-align: middle;
	margin-right: 10px;
}
.user .icon[data-v-60e458e0]{
	width: 17px;
    height: 17px;
    margin-left: 5px;
    vertical-align: middle;
}
.user-x[data-v-60e458e0]{
	position: absolute;
	right: 20px;
	top: 70px;
	width: 100px;
	height: 100px;
	z-index: 1000;
}
.user-x ul li[data-v-60e458e0]{
	list-style: none;
	width: 100px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	cursor: pointer;
}
.user-x ul li a[data-v-60e458e0]{
	display: block;
}
.user-x ul li[data-v-60e458e0]:hover{
	background: rgba(7,17,27,.5);
}



